"use client";

import { Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer } from "recharts";

export default function PersonaRadar({ personas }: { personas: any[] }) {
  if (!personas || personas.length === 0) return null;

  // Assume each persona has skill values like comprehension, attention, etc.
  return (
    <div className="h-96">
      <h2 className="text-xl font-semibold mb-2">Persona Profiles</h2>
      <ResponsiveContainer width="100%" height="100%">
        <RadarChart data={personas}>
          <PolarGrid />
          <PolarAngleAxis dataKey="persona" />
          <PolarRadiusAxis angle={30} domain={[0, 100]} />
          <Radar
            name="Comprehension"
            dataKey="comprehension"
            stroke="#3b82f6"
            fill="#3b82f6"
            fillOpacity={0.4}
          />
          <Radar
            name="Attention"
            dataKey="attention"
            stroke="#10b981"
            fill="#10b981"
            fillOpacity={0.4}
          />
          <Radar
            name="Focus"
            dataKey="focus"
            stroke="#f59e0b"
            fill="#f59e0b"
            fillOpacity={0.4}
          />
          <Radar
            name="Retention"
            dataKey="retention"
            stroke="#ef4444"
            fill="#ef4444"
            fillOpacity={0.4}
          />
        </RadarChart>
      </ResponsiveContainer>
    </div>
  );
}
